"use client";

import { useState, useRef, useEffect } from "react";
import { Menu, X } from "lucide-react"; // นำเข้าไอคอน Menu และ X จาก lucide-react

export default function HorizontalScrollPage() {
  // State เพื่อควบคุมการเปิด/ปิดเมนูสำหรับอุปกรณ์มือถือ
  const [isOpen, setIsOpen] = useState(false);

  // Ref สำหรับอ้างอิงถึง container ที่จะทำการเลื่อนแนวนอน
  const scrollContainerRef = useRef(null);

  // ฟังก์ชันสำหรับเลื่อน scroll container ไปทางซ้ายทีละหน้าจอ
  const scrollLeft = () => {
    if (scrollContainerRef.current) {
      scrollContainerRef.current.scrollBy({
        left: -window.innerWidth, // เลื่อนไปทางซ้ายเท่ากับความกว้างของหน้าจอ
        behavior: "smooth", // ทำให้การเลื่อนเป็นไปอย่างนุ่มนวล
      });
    }
  };

  // ฟังก์ชันสำหรับเลื่อน scroll container ไปทางขวาทีละหน้าจอ
  const scrollRight = () => {
    if (scrollContainerRef.current) {
      scrollContainerRef.current.scrollBy({
        left: window.innerWidth, // เลื่อนไปทางขวาเท่ากับความกว้างของหน้าจอ
        behavior: "smooth", // ทำให้การเลื่อนเป็นไปอย่างนุ่มนวล
      });
    }
  };

  // useEffect สำหรับการเลื่อนไปยัง Section 3 (ซึ่งเป็น Section แรกในตอนนี้) เมื่อคอมโพเนนต์โหลด
  useEffect(() => {
    if (scrollContainerRef.current) {
      scrollContainerRef.current.scrollTo({
        left: 0, // Section 3 อยู่ที่ตำแหน่งเริ่มต้น (0)
        behavior: "smooth", // ทำให้การเลื่อนเป็นไปอย่างนุ่มนวล
      });
    }
  }, []); // [] หมายความว่า useEffect จะทำงานเพียงครั้งเดียวหลังจากการเรนเดอร์ครั้งแรก

  return (
    <>
      {/* Navbar ส่วนหัวของหน้าเว็บ */}
      <nav className="w-full fixed top-0 left-0 z-50 shadow-md bg-white bg-opacity-80 backdrop-blur-md">
        <div className="w-full px-6 py-4 flex justify-between items-center">
          {/* โลโก้และชื่อเว็บไซต์ */}
          <a
            href="/"
            className="flex items-center space-x-3"
            aria-label="Go to home page"
          >
            <img
              src="/Muha.png" // Path ไปยังรูปโลโก้ (ต้องอยู่ในโฟลเดอร์ public)
              alt="MyWebsite Logo"
              width={40}
              height={40}
              className="rounded-full"
            />
            <span className="text-2xl font-bold text-blue-600 tracking-wide">
              MyWebsite
            </span>
          </a>

          {/* เมนูสำหรับ Desktop (ซ่อนบนมือถือ) */}
          <div className="hidden md:flex space-x-8 text-lg font-medium">
            <a
              href="#section3"
              className="text-gray-700 hover:text-blue-600 transition"
            >
              การ์ด
            </a>
            <a
              href="#section1"
              className="text-gray-700 hover:text-blue-600 transition"
            >
              หน้าแรก
            </a>
            <a
              href="#section2"
              className="text-gray-700 hover:text-blue-600 transition"
            >
              เกี่ยวกับเรา
            </a>
            {/* ใช้ Link คอมโพเนนต์สำหรับหน้าภายใน Next.js เพื่อประโยชน์ด้านการ Routing */}
            <a
              href="/contact"
              className="text-gray-700 hover:text-blue-600 transition"
            >
              ติดต่อ
            </a>
            <a
              href="https://github.com/peachpeach06/frontend"
              target="_blank" // เปิดในแท็บใหม่
              rel="noopener noreferrer" // เพิ่มความปลอดภัย
              className="text-gray-700 hover:text-blue-600 transition"
            >
              GitHub
            </a>
          </div>

          {/* ปุ่ม Toggle เมนูมือถือ (แสดงบนมือถือ) */}
          <div className="md:hidden">
            <button
              onClick={() => setIsOpen(!isOpen)}
              className="text-gray-700 focus:outline-none"
              aria-label={isOpen ? "Close menu" : "Open menu"}
              aria-expanded={isOpen}
            >
              {isOpen ? (
                <X className="w-7 h-7" />
              ) : (
                <Menu className="w-7 h-7" />
              )}
            </button>
          </div>
        </div>

        {/* เมนูมือถือแบบ Dropdown (แสดงเมื่อ isOpen เป็น true) */}
        {isOpen && (
          <div className="md:hidden bg-white bg-opacity-95 px-6 py-4 space-y-3 text-base font-medium shadow-lg rounded-b-lg">
            <a
              href="#section3"
              className="block text-gray-700 hover:text-blue-600 transition"
              onClick={() => setIsOpen(false)} // ปิดเมนูเมื่อคลิก
            >
              หน้าแรก
            </a>
            <a
              href="#section2"
              className="block text-gray-700 hover:text-blue-600 transition"
              onClick={() => setIsOpen(false)} // ปิดเมนูเมื่อคลิก
            >
              เกี่ยวกับเรา
            </a>
            {/* ใช้ Link คอมโพเนนต์สำหรับหน้าภายใน Next.js */}
            <a
              href="/contact"
              className="block text-gray-700 hover:text-blue-600 transition"
              onClick={() => setIsOpen(false)} // ปิดเมนูเมื่อคลิก
            >
              ติดต่อ
            </a>
            <a
              href="https://github.com/peachpeach06/frontend"
              target="_blank"
              rel="noopener noreferrer"
              className="block text-gray-700 hover:text-blue-600 transition"
              onClick={() => setIsOpen(false)} // ปิดเมนูเมื่อคลิก
            >
              GitHub
            </a>
          </div>
        )}
      </nav>

      {/* ปุ่มเลื่อนซ้าย-ขวา ที่อยู่ตรงกลางด้านข้าง */}
      <button
        onClick={scrollLeft}
        aria-label="Scroll Left"
        className="fixed top-1/2 left-4 -translate-y-1/2 z-50 bg-white bg-opacity-70 hover:bg-opacity-90 text-gray-700 rounded-full p-3 shadow-lg"
      >
        ◀
      </button>
      <button
        onClick={scrollRight}
        aria-label="Scroll Right"
        className="fixed top-1/2 right-4 -translate-y-1/2 z-50 bg-white bg-opacity-70 hover:bg-opacity-90 text-gray-700 rounded-full p-3 shadow-lg"
      >
        ▶
      </button>

      {/* Background ภาพเดียวกันทั้งหน้า */}
      <div className="fixed inset-0 -z-10">
        <img
          src="/Mutu.jpg" // Path ไปยังรูปภาพพื้นหลัง (ต้องอยู่ในโฟลเดอร์ public)
          alt="Background Image"
          style={{ width: "100%", height: "100%", objectFit: "cover" }} // ครอบคลุมพื้นที่โดยไม่บิดเบี้ยว
        />
      </div>

      {/* Container หลักที่เลื่อนแนวนอน */}
      <div
        ref={scrollContainerRef}
        className="flex overflow-x-auto scroll-smooth snap-x snap-mandatory"
        style={{ height: "100vh", scrollBehavior: "smooth" }}
      >
        {/* Section 3 (อยู่ตำแหน่งแรกสุดในตอนนี้) */}
        <section
          id="section3"
          className="snap-start flex-shrink-0 w-screen h-screen relative"
        >
          <div className="relative z-10 flex items-center justify-center h-full text-center text-white px-4">
            <h1 className="text-5xl font-bold drop-shadow-md">ส่วนของการ์ด</h1>
          </div>
        </section>

        {/* Section 1 */}
        <section
          id="section1"
          className="snap-start flex-shrink-0 w-screen h-screen relative"
        >
          <div className="relative z-10 pt-40 text-center text-white px-4">
            <h1 className="text-5xl font-bold drop-shadow-md">
              ยินดีต้อนรับสู่ MyWebsite!
            </h1>
            <p className="mt-4 text-lg drop-shadow-sm max-w-2xl mx-auto">
              เรามุ่งมั่นที่จะนำเสนอประสบการณ์การใช้งานที่ยอดเยี่ยมและข้อมูลที่เป็นประโยชน์แก่คุณ
              ไม่ว่าคุณจะกำลังมองหาแรงบันดาลใจ ความรู้ หรือการเชื่อมต่อ
              เราพร้อมให้บริการคุณอย่างเต็มที่
            </p>
            <p className="mt-2 text-md drop-shadow-sm max-w-2xl mx-auto">
              สำรวจหน้าต่างๆ ของเราเพื่อค้นหาสิ่งที่คุณสนใจ
              และอย่าลังเลที่จะติดต่อเราหากมีข้อสงสัยใดๆ
            </p>
          </div>
        </section>

        {/* Section 2 */}
        <section
          id="section2"
          className="snap-start flex-shrink-0 w-screen h-screen relative"
        >
          <div className="relative z-10 flex flex-col items-center justify-center h-full text-center text-white px-4">
            <h2 className="text-4xl font-bold drop-shadow-md mb-4">
              เกี่ยวกับเรา
            </h2>
            <p className="text-lg drop-shadow-sm max-w-2xl mx-auto">
              MyWebsite
              ก่อตั้งขึ้นด้วยความตั้งใจที่จะเป็นแหล่งรวมข้อมูลและความรู้ที่เข้าถึงง่ายและเป็นประโยชน์
              เราเชื่อว่าการแบ่งปันข้อมูลที่มีคุณภาพจะช่วยให้ทุกคนเติบโตและพัฒนาไปพร้อมกัน
            </p>
            <p className="mt-2 text-md drop-shadow-sm max-w-2xl mx-auto">
              ทีมงานของเราประกอบด้วยผู้เชี่ยวชาญที่มีความมุ่งมั่นในการสร้างสรรค์เนื้อหาและประสบการณ์
              ที่ดีที่สุด เรายินดีรับฟังความคิดเห็นและข้อเสนอแนะจากคุณเสมอ
              เพื่อให้ MyWebsite
              เป็นแพลตฟอร์มที่ตอบโจทย์ความต้องการของคุณได้อย่างแท้จริง
            </p>
          </div>
        </section>
      </div>

      {/* ส่วนของการ์ดที่อยู่ด้านล่างของทุกหน้า */}
      <div className="fixed bottom-0 left-0 w-full z-40 pb-12 px-4 flex justify-center">
        <div className="flex gap-6 overflow-x-auto w-full max-w-4xl scrollbar-thin scrollbar-thumb-gray-400 scrollbar-track-gray-200">
          {/* Card แต่ละใบ flex-shrink-0 เพื่อไม่ให้ย่อขนาด */}
          <div className="bg-white bg-opacity-90 rounded-xl shadow-lg p-6 w-64 flex-shrink-0">
            <h2 className="text-xl font-bold mb-2 text-blue-600">
              การ์ดที่ 1: นวัตกรรม
            </h2>
            <p className="text-gray-700">
              ค้นพบเทคโนโลยีใหม่ล่าสุดที่กำลังเปลี่ยนแปลงโลกของเรา
              ด้วยนวัตกรรมที่ก้าวล้ำเพื่ออนาคตที่ดีกว่า
            </p>
          </div>

          <div className="bg-white bg-opacity-90 rounded-xl shadow-lg p-6 w-64 flex-shrink-0">
            <h2 className="text-xl font-bold mb-2 text-green-600">
              การ์ดที่ 2: ความยั่งยืน
            </h2>
            <p className="text-gray-700">
              เรียนรู้เกี่ยวกับแนวทางปฏิบัติที่ยั่งยืนและวิธีที่เราสามารถช่วยรักษาสิ่งแวดล้อมเพื่อคนรุ่นต่อไป
            </p>
          </div>

          <div className="bg-white bg-opacity-90 rounded-xl shadow-lg p-6 w-64 flex-shrink-0">
            <h2 className="text-xl font-bold mb-2 text-purple-600">
              การ์ดที่ 3: ชุมชน
            </h2>
            <p className="text-gray-700">
              เชื่อมต่อกับชุมชนของเราและร่วมสร้างสรรค์สิ่งดีๆ ไปด้วยกัน
              เพื่อสังคมที่เข้มแข็งและอบอุ่น
            </p>
          </div>
        </div>
      </div>
    </>
  );
}
